.todoForm_inputField__3blFb{
    margin: 20px 0;
    width: 100%;
    display: flex;
    height: 45px;
  }

  .todoForm_inputField__3blFb input{
    width: 85%;
    height: 100%;
    outline: none;
    border-radius: 3px;
    border: 1px solid #ccc;
    font-size: 17px;
    padding-left: 15px;
    transition: all 0.3s ease;
  }
  .todoForm_inputField__3blFb input:focus{
    border-color: #8E49E8;
  }
  .todoForm_inputField__3blFb button{
    width: 50px;
    height: 100%;
    border: none;
    color: #fff;
    margin-left: 5px;
    font-size: 21px;
    outline: none;
    background: #8E49E8;
    cursor: pointer;
    border-radius: 3px;
    opacity: 0.6;
    pointer-events: none;
    transition: all 0.3s ease;
  }
  .todoForm_inputField__3blFb button:hover{
    background: #721ce3;
  }
  .todoForm_inputField__3blFb button.todoForm_active__33ybU{
    opacity: 1;
    pointer-events: auto;
  }
.todoList_todoList__3Mq2J{
    max-height: 250px;
    overflow-y: auto;
  }

.todoList_todoList__3Mq2J li{
    position: relative;
    list-style: none;
    height: 45px;
    line-height: 45px;
    margin-bottom: 8px;
    background: #f2f2f2;
    border-radius: 3px;
    padding: 0 15px;
    cursor: default;
    overflow: hidden;
}
.todoList_todoList__3Mq2J li .todoList_icon__24UQu{
    position: absolute;
    right: -45px;
    background: #e74c3c;
    width: 45px;
    text-align: center;
    color: #fff;
    border-radius: 0 3px 3px 0;
    cursor: pointer;
    transition: all 0.2s ease;
}
.todoList_todoList__3Mq2J li:hover .todoList_icon__24UQu{
    right: 0px;
}
